





























































import { Vue, Component } from 'vue-property-decorator'
import { sendVerifyEmail } from 'utils/api'

@Component({})
export default class ForgetPassword extends Vue {
  active: number = 0

  infoForm: any = {
    email: '',
    idCardNumber: '',
    name: ''
  }
  infoRules: any = {
    email: [
      {
        required: true,
        validator: (rule, value, callback) => {
          if (value === '') {
            callback(new Error('請輸入郵箱'))
          } else if (!/^[A-Za-z\d]+([-_.][A-Za-z\d]+)*@([A-Za-z\d]+[-.])+[A-Za-z\d]{2,4}$/.test(value)) {
            callback(new Error('郵箱格式錯誤'))
          } else {
            callback()
          }
        },
        trigger: 'blur'
      }
    ],
    idCardNumber: [
      {
        required: true,
        validator: (rule, value, callback) => {
          if (value === '') {
            callback(new Error('請輸入身份證號'))
          } else if (!/^[A-Z][0-9]{9}$/.test(value)) {
            callback(new Error('請檢查身份證是否正確'))
          } else {
            callback()
          }
        }, trigger: 'blur'
      }
    ],
    name: [
      { required: true, message: '請輸入姓名', trigger: 'change' }
    ]
  }
  infoFormWidth: string = '120px'

  verifyForm: any = {
    emailVerifyCode: ''
  }
  verifyRules: any = {
    emailVerifyCode: [
      { required: true, message: '請輸入驗證碼', trigger: 'blur' }
    ]
  }
  verifyFormWidth: string = '100px'
  hasSendVerifyCode: boolean = false
  sendCodeInterval: number = 60

  resetForm: any = {
    password: '',
    confirmPassword: ''
  }
  resetRules: any = {
    password: [
      { required: true, message: '請輸入密碼', trigger: 'change' }
    ],
    confirmPassword: [
      {
        required: true,
        validator: (rule, value, callback) => {
          if (value === '') {
            callback(new Error('請再次輸入密碼'))
          } else if (value !== this.resetForm.password) {
            callback(new Error('兩次輸入密碼不一致'))
          } else {
            callback()
          }
        }, trigger: 'blur'
      }
    ],
  }
  resetFormWidth: string = '100px'

  sendVerifyCodeAgain () {
    sendVerifyEmail().then((res) => {
      this.hasSendVerifyCode = true
      this.sendCodeInterval = 60
      let countDown = window.setInterval(() => {
        --this.sendCodeInterval
        if (this.sendCodeInterval < 0) {
          window.clearInterval(countDown)
          this.hasSendVerifyCode = false
          this.sendCodeInterval = 60
        }
      }, 1000)
    }).catch((err) => {
      this.$message({
        message: '發送失敗',
        type: 'error'
      })
    })
  }

  checkInfo () {
    // let form: any = this.$refs.infoForm
    // form.validate((valid) => {
    //   if (valid) {
    //     ++this.active
    //   } else {
    //     this.$message({
    //       message: '內容有誤，請重新填寫',
    //       type: 'error'
    //     })
    //     return false
    //   }
    // })
    ++this.active
  }

  verifyEmail () {
    // let form: any = this.$refs.verifyForm
    // form.validate((valid) => {
    //   if (valid) {
    //     ++this.active
    //   } else {
    //     this.$message({
    //       message: '驗證碼錯誤',
    //       type: 'error'
    //     })
    //     return false
    //   }
    // })
    ++this.active
  }

  finishReset () {
    let form: any = this.$refs.resetForm
    form.validate((valid) => {
      if (valid) {
        this.$message({
          message: '密碼重置成功',
          type: 'success'
        })
        setTimeout(function () {
          this.$router.push('/')
        }.bind(this), 1000)
      } else {
        this.$message({
          message: '驗證碼錯誤',
          type: 'error'
        })
        return false
      }
    })
  }
}



